body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  background-color: #6D67E4;
}
h1{
  margin-left: 3%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/******* Contact div *********/
.App-Inner{

  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-left: 20px;
}

/****** Style for each contact *******/

.item-content{
  width: 300px;
  color: white;
  background-color: #EC7272;
  border-radius: 15px;
  margin: 10px;
  padding: 10px;
}
/********* Heading and form styling ********/

.heading-form{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.input{
  margin: 5px;
  border: none;
  outline: none;
  height: 20px;
}
summary{
  color: white;
  cursor: pointer;
}
button{
  background-color:white;
  outline: none;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: .7s;
}
button:hover{
  background-color: greenyellow;
}

/***** Update and delete contact styling *****/
.update-delete{
  display: flex;
  justify-content: space-around;
  cursor: pointer;
  color: black;
}